* {
  padding: 0;
  margin: 0;
}

.App {
  height:100vh;
  width: 100vw;
  background-image: linear-gradient(0deg, rgba(0,0,0,0.6) 0%, rgba(0,0,0,0.0) 50%, rgba(0,0,0,0.6) 100%), url('./images/background.png');
  background-position: center;
  background-size: cover;
}

.league {
  font-family: 'League';
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0,0,0,0.3);
  color: rgba(154, 255, 221, 0.92);
  line-height: 1;
  height: 8vh;
}

.title {
  text-align: center;
  font-size: 1.2rem;
}

.header-item {
  width: 150px;
  font-size: 16px;
  text-align: end;
  padding-right: 10px;
}

.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 92vh;
}

.card {
  height: 180px;
  width: 160px;
  border: solid black 2px;
  color: rgba(154, 255, 221, 0.92);
  font-size: 16px;
  text-align: center;
  margin:30px;
  background-color: rgba(0,0,0,0.5);
}

.card:hover {
  
  cursor: pointer;
}

.card-img {
  overflow: hidden;
}

.card img {
  transition: 0.3s all ease-out;
}

.card:hover img {
  transform: scale(1.3);
}

.card-name {
  margin-top: 2px;
}



.card-update {
  cursor: pointer;
  background-color: transparent;
  width:160px;
  height:180px;
  perspective: 1000px;
  margin: 30px;
}

.card-update:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px, rgba(0, 0, 0, 0.05) 0px 5px 10px;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.card-front, .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.card-front {
  background-color: rgba(0,0,0,0.4);
  color: rgba(154, 255, 221, 0.92);
  font-size: 16px;
  border: solid black 2px;
}

.card-back {
  background-color: rgba(0,0,0,0.4);
  color: rgba(154, 255, 221, 0.92);
  font-size: 16px;
  transform: rotateY(180deg);
  height: 180px;
  overflow: hidden;
  border: solid black 2px;
}

.card-back img {
  transition: 0.3s all ease-out;
}

.card-back:hover img {
  transform: scale(1.3);
}

.menu {
  text-align: center;
}

.menu-p {
  background-color: rgba(0,0,0,0.8);
  padding: 60px;
  padding-top: 40px;
  padding-bottom: 40px;
  color: rgba(154, 255, 221, 0.92);
  font-size:16px;
  line-height:2;
  border-radius: 16px;
}

.start-btn {
  cursor: pointer;
  margin-top:20px;
  font-size:32px;
  font-family: 'League';
  border-radius: 8px;
  padding:4px;
  background-color: rgba(154, 255, 221, 0.92);
}